import { authMeTC} from './auth-reducer';
const INITIALIZED_SUCCESS = 'INITIALIZED_SUCCESS';
const SET_ERROR_COMPONENT = 'SET_ERROR_COMPONENT';

let initialState = {
    initialized: false,
    renderErrorComponent: false
}
const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case INITIALIZED_SUCCESS: {
            return {
                ...state,
                initialized: true
            }
        }
        case SET_ERROR_COMPONENT: {
            return {
                ...state,
                renderErrorComponent: action.status
            }
        }
        default: {
            return state
        }
    }
} 
export const initializedSuccess = () => ({type: INITIALIZED_SUCCESS})
export const setErrorComponentSuccess = (status) => ({type: SET_ERROR_COMPONENT,status})
export const initializeApp = () => (dispatch) => {
    let promise = dispatch(authMeTC());
    promise.then(() => {
        dispatch(initializedSuccess());
    })
    
}
export const showErrorComponent = () => (dispatch) => {
    dispatch(setErrorComponentSuccess(true));
    setTimeout(() => {
        dispatch(setErrorComponentSuccess(false));
    },2000)
}
export default appReducer;

