import { profileApi } from "../api/api";
import { usersApi } from './../api/api';
import { stopSubmit } from 'redux-form';
import { showErrorComponent } from "./app-reducer";

const ADD_POST = 'ADD-POST';
const DELETE_POST = 'DELETE_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';
const SET_PROFILE_UPDATE = 'SET_PROFILE_UPDATE';
let initialState = {
    posts: [
        { id: 1, message: 'Hi, how are you?', likesCount: 12 },
        { id: 2, message: 'It\'s my first post', likesCount: 11 },
        { id: 3, message: 'Blabla', likesCount: 11 },
        { id: 4, message: 'Dada', likesCount: 11 }
    ],
    profile: null,
    status: '',
    profileUpdateStatus: null
}
const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let newPost = {
                id: 5,
                message: action.newPostText,
                likesCount: 0
            };
            return {
                ...state,
                posts: [...state.posts, newPost],

            }
        }
        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: action.profile,
            }
        }
        case DELETE_POST: {
           return {
               ...state,
               posts: state.posts.filter(p => p.id != action.postId)
           } 
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status,
            }
        }
        case SET_PROFILE_UPDATE: {
            return {
                ...state,
                profileUpdateStatus: action.updateProfileStatus,
            }
        }
        case SAVE_PHOTO_SUCCESS: {
            return {
                ...state,
                profile: {...state.profile,photos: action.photos},
            }
        }
        default:
            return state;
    }

}
export const addPostActionCreator = (newPostText) => ({ type: ADD_POST, newPostText });
export const deletePost = (postId) => ({type: DELETE_POST, postId});
export const setStatus = (status) => ({ type: SET_STATUS, status });
export const setUsersProfile = (profile) => ({ type: SET_USER_PROFILE, profile });
export const savePhotoSuccess = (photos) => ({ type: SAVE_PHOTO_SUCCESS, photos });
export const setProfileUpdate = (updateProfileStatus) => ({ type: SET_PROFILE_UPDATE, updateProfileStatus });
export const setProfileUser = (userId) => {
    return async (dispatch) => {
        let response = await profileApi.getProfile(userId)
        dispatch(setUsersProfile(response.data));
    }
}

export const getStatus = (userId) => {
    return async (dispatch) => {
        let response = await profileApi.getStatus(userId)
        dispatch(setStatus(response.data));
    }
}
export const updateStatus = (status) => {
    return async (dispatch) => {
        try {
            const response = await profileApi.updateStatus(status);
            if (response.data.resultCode === 0) {
                dispatch(setStatus(status));
            }
        } catch(error) {
            dispatch(showErrorComponent());
        }
    }
}
export const savePhoto = (file) => {
    return async (dispatch) => {
        let response = await usersApi.savePhoto(file);
        if (response.data.resultCode === 0) {
            dispatch(savePhotoSuccess(response.data.data.photos));
        }
    }
}
export const saveProfile = (profile) => {
    return async (dispatch,getState) => {
        const userId = getState().auth.id;
        const response = await usersApi.saveProfile(profile);
        if (response.data.resultCode === 0) {
            dispatch(setProfileUser(userId));
            dispatch(setProfileUpdate('success'));
        } else {
            let message = response.data.messages[0]
            .slice(
                response.data.messages[0].indexOf('>') + 1,
                response.data.messages[0].indexOf(')')).toLocaleLowerCase();
            dispatch(stopSubmit("edit-profile",{contacts:{[message]:response.data.messages[0]}}))
            dispatch(setProfileUpdate('error'));
        }
    }
}

export default profileReducer;