import { authApi, securityAPI } from "../api/api";
import { stopSubmit } from "redux-form";

const SET_USER_DATA = 'auth/SET_USER_DATA';
const GET_CAPTCHA_SUCCESS = 'auth/GET_CAPTCHA_SUCCESS';

let initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false,
    captchaUrl: null
}
let authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA: {
            return {

                ...state,
                ...action.data

            }
        }
        case GET_CAPTCHA_SUCCESS: {
            return {

                ...state,
                ...action.payload

            }
        }

        default:
            return state;
    }

}
export const setUserAuthDataAC = (id, email, login, isAuth) => ({ type: SET_USER_DATA, data: { id, email, login, isAuth } });
export const getCaptchaUrlSuccess = (captchaUrl) => ({ type: GET_CAPTCHA_SUCCESS, payload: {captchaUrl}});
export const authMeTC = () => {
    return async (dispatch) => {
        let response = await authApi.authMe()
        if (response.data.resultCode === 0) {
            let { id, login, email } = response.data.data;
            dispatch(setUserAuthDataAC(id, login, email, true));
        }
    }
}
export const login = (email, password, rememberMe,captcha) => {

    return async (dispatch) => {

        let response = await authApi.login(email, password, rememberMe,captcha)
        if (response.data.resultCode === 0) {
            dispatch(authMeTC());
        } 
        else {
            if(response.data.resultCode === 10) {
                dispatch(getCaptchaUrl());
            }
            let messages = response.data.messages.length > 0 ? response.data.messages[0] : 'Some error';
            dispatch(stopSubmit('login', {
                _error: messages
            }));
        }
    }
}
export const getCaptchaUrl = () => {
    return async (dispatch) => {
        const response = await securityAPI.getCaptcha()
        const captchaUrl = response.data.url;
        dispatch(getCaptchaUrlSuccess(captchaUrl));
    }
}
export const logout = () => {
    return async (dispatch) => {
        let response = await authApi.logout()
        if (response.data.resultCode === 0) {
            dispatch(setUserAuthDataAC(null, null, null, false));
        }
    }
}

export default authReducer;